.p404-page-container {
  background-color: #e6e6e6;
  width: 100vw;
  height: 100vh; }

.p404-image {
  width: 1080px;
  margin: 0 auto;
  -o-object-fit: contain;
     object-fit: contain;
  display: block;
  top: 30vh;
  position: relative; }

.p404-goBack-btn {
  border: none;
  color: white;
  background-color: #f37070;
  padding: 25px;
  font-size: 25px;
  font-weight: 700;
  -webkit-box-shadow: 0px 3px 6px #e6e6e6;
          box-shadow: 0px 3px 6px #e6e6e6;
  text-transform: uppercase;
  text-align: center;
  display: block;
  position: relative;
  top: 30vh;
  margin: 0 auto;
  cursor: pointer; }

@media (max-width: 1180px) {
  .p404-image {
    width: 80vw; }
  .p404-goBack-btn {
    font-size: 14px;
    padding: 10px; } }



